// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
//需區隔第三方套件及自建立

import App from './App'
import router from'./router'
import 'bootstrap';
import Loading from 'vue-loading-overlay'; //瀏覽樣式
import 'vue-loading-overlay/dist/vue-loading.css'; //瀏覽的ccs樣式
import './bus'; //將bus.vue直接掛在Vue主元件的下面

import currencyFilter from './filters/currency';
import dateFilter from './filters/date';

import VeeValidate from 'vee-validate';
import zhTW from 'vee-validate/dist/locale/zh_TW';
import VueI18n from 'vue-i18n';





Vue.use(VueAxios, axios)
Vue.config.productionTip = false

axios.defaults.withCredentials = true; //將API撈取的資料也備份在前端記憶體
Vue.component('Loading',Loading); //全域啟用瀏覽中的樣式
Vue.filter('currency',currencyFilter); //全域啟用瀏覽中的樣式
Vue.filter('date', dateFilter);
Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: 'zhTW'
})
Vue.use(VeeValidate, {
  i18n,
  dictionary: {
    zhTW
  }
});


/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,

  i18n,
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
});

router.beforeEach((to, from, next) => {
  console.log('to',to, 'from',from, 'next',next);

  if(to.meta.requiresAuth){ //需審核的頁面放置追蹤碼後，運行下方判定公式
    console.log('need to check');
    const api = `${process.env.APIPATH}/api/user/check`; //依登入api說明新增(左：路徑，右：API功能) 有成功到這一層嗎
    axios.post(api).then((response) => { //api後方連結data裡的user
        console.log(response.data);
        if(response.data.success){
          next();
        }else{
          next({
            path: '/login',

        });
      }
      });
    }
        else{
          next();
        }
  });

  // if(to.meta.requiresAuth){ //需審核的頁面放置追蹤碼後，運行下方判定公式
