import Vue from 'vue'
import VueRouter from 'vue-router'
//import Home from '@/components/HelloWorld';
import Login from '@/components/pages/Login';
import Dashboard from '@/components/Dashboard';
import Products from '@/components/pages/Products';
import Order from '@/components/pages/Order';
import Coupon from '@/components/pages/Coupon';
import CustomerOrder from '@/components/pages/CustomerOrder';
import CustomerCheckout from '@/components/pages/CustomerCheckout';





//自訂的分類元件    

Vue.use(VueRouter);

export default new VueRouter({
    routes:[

        { //當輸入不對應網址，會導入至login頁面
            path:'*',
            redirect:'login',
        },
        // {
        //     path:'/', //虛擬路徑
        //     name:'首頁', //元件名稱
        //     component: Home, //對應文件
        //     meta: { requiresAuth: true } //判斷是否有經過認證登入，驗證為true才開放進入此頁面

        // },
        {
            path:'/login',
            name:'Login',
            component: Login,
        },
        {
            path:'/admin',
            name:'Dashboard',
            component: Dashboard,
            children:[
                {   path:'products', //巢狀http://admin/products
                    name:'products',
                    component: Products,
                    meta: { requiresAuth: true },
                },

                {   path:'orders', //巢狀http://admin/orders
                    name:'orders',
                    component: Order,
                },

                {   path:'coupons', //巢狀http://admin/coupons
                    name:'coupons',
                    component: Coupon,
                },
            ]

            
        },

        {
            path:'/',
            name:'Dashboard',
            component: Dashboard,
            children:[
                {   path:'customer_order', //巢狀http://admin/products //路徑建議小寫
                    name:'CustomerOrder',
                    component: CustomerOrder,
                },

                {   path:'customer_checkout/:orderId', //巢狀http://admin/products //路徑建議小寫
                    name:'CustomerCheckout',
                    component: CustomerCheckout,
                },

            ]

            
        },
    ],
});


